	module.exports = {"data":{"wordpressPost":{"title":"All the reasons you need to know your skin type","content":"<p>You’re likely across the fact that skin comes in a variety of types, but if you have little to no idea which type you are you’re not alone. The vast majority of us either have no clue or, worse, have misdiagnosed our skin type altogether. That’s a problem because knowing your type does make caring for your skin much easier.</p>\n<p>“It does help to know the basics of skin analysis,” says skin therapist Christine Clais, author of French Complexion &#8211; The Secret to Beautiful Skin at Any Age (Viking).</p>\n<p>“Failing to correctly identify your skin type and the condition it is in usually leads to choosing the wrong products and worsening your skin’s condition.”</p>\n<p>Charlotte Turner, head of training for the French beauty house Clarins agrees. The vast majority of skincare (and even some cosmetic) ranges are specifically formulated for specific skin types, says Charlotte.</p>\n<p>“If you select products that do not match your skin type it can lead to imbalances which can exacerbate signs of premature ageing such as fine lines and wrinkles.”</p>\n<p>Nobody wants that. So here’s a sneaky guide to identifying your skin type today &#8211; read it to ID which type you are. Then, you can be 100 per cent confident you’re choosing the products and treatments that suit you best.</p>\n<p><strong>Oily Skin</strong></p>\n<p>Skin is affected by genes and your hormones too. This means it can change at various periods of your life &#8211; think puberty and menopause for example. Still, if for the majority of the time your skin general appears shiny and has enlarged pores, occasionally develops blackheads and whiteheads too, plus an odd pimple or even acne, you’re likely to be an oily type.</p>\n<p>Tip: Try moisturisers and also makeup that have mattifying ingredients to tone down shine.</p>\n<p><strong>Normal Skin</strong></p>\n<p>No enlarged pores are visible and on the whole, you’ve got a nice little glow going on.</p>\n<p>Your skin is smooth, plump looking, has few to no blackheads (or whiteheads either) and pimples are rare. You’re normal.</p>\n<p>Tip: Your skin is gorgeous. Keep it that way by wearing an SPF all year round.</p>\n<p><strong>Combination Skin</strong></p>\n<p>This type is particularly common and easy to recognise. If your T-zone (the forehead, nose and areas around the mouth and chin) appear shiny then you have combination skin, says Charlotte Turner.</p>\n<p>Tip: Use oily skin products in the T-zone but opt for normal elsewhere.</p>\n<p><strong>Dry Skin</strong></p>\n<p>Can feel tight and touchy and even rough to the touch. Often looks dry or slightly flaky and is prone to fine lines and ageing. Breakouts are very rare but conditions such as dermatitis and even eczema are common.</p>\n<p>Tip: Take shorter showers, use mild, gentle cleansers and apply a rich moisturiser as needed.<br />\n<em>Clarins Cleansing Milk with Alpine Herbs for Dry/Normal Skin, $44. clarins.com or 02 9663 4277.</em></p>\n<p>Removes dirt and grime yet add moisture and neutralises the drying effect of water.</p>\n<p><em>Giorgio Armani ARMANI PRIMA Glow-On Moisturising Balm, $128. 1300 651 991</em>.</p>\n<p>Hydrates even the driest skin leaving it radiant and dewy, plus it’s a perfect pre-foundation base.</p>\n<p><em>Homedics Radiance Microdermabrasion Device, $249. homedics.com.au or 03 8756 6512.</em></p>\n<p>A handheld microdermabrasion device that whips off dead skin cells and stimulates the skin beneath. Particularly suited to oily skin types who are battling visible pores and breakouts.</p>\n<p><em>Olay Complete UV Protection Moisture Lotion SPF 15 Normal/Dry, $14.99. olay.com.au or 1800 028 280.</em></p>\n<p>Non-Greasy yet deeply hydrating, this lotion b<br />\noosts elasticity and reduces the appearance of fine lines.</p>\n<p><em>Nivea 2in1 Wash &amp;amp; Scrub, $9.97. nivea.com.au or 1800 103 023.</em></p>\n<p>A gentle exfoliating scrub and cleansing gel which removes excess oil and sebum preventing shine. Perfect for oily and/or combination skin.</p>\n<p><em>La Prairie Cellular Swiss Ice Crystal Transforming SPF30, $240. laprairie.com.au or 1800 649 849.</em></p>\n<p>A colour correcting cream that brightens, evens skin tone, refines pores and provides glow. Suitable for all skins.</p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/02/02151801/freestocks-org-141810.jpg"},"categories":[{"slug":"skincare","name":"Skincare"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"035d17b6-e63b-5736-9a60-4077a4a02f0f"}}

